/*задайте фоновые цвета и фоновые изображения для блоков;
параметры текста: шрифты, размеры шрифта, межстрочные расстояния, цвет текста;
добавьте внутренние и внешние отступы и обводки для крупных сеточных блоков.*/
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"],
input[type="radio"],
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  font-size: 14px;
  line-height: 18px;
  font-weight: normal;
  text-transform: uppercase;
  font: inherit;
  text-align: center;
  border: none;
}
@media (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 20px;
    font-weight: normal;
  }
}
.button--green {
  color: #ffffff;
  background-color: #68b738;
}
.button--green:hover,
.button--green:focus {
  background-color: #5eaa2f;
}
.button--green:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.button--gray {
  color: #444444;
  background-color: #f2f2f2;
}
.button--gray:hover,
.button--gray:focus {
  background-color: #ebebeb;
}
.button--gray:active {
  background-color: #ebebeb;
  color: rgba(68, 68, 68, 0.3);
}
.title {
  width: auto;
  background-color: white;
  box-sizing: border-box;
  position: relative;
}
.title::after {
  content: "";
  width: 100vw;
  position: absolute;
  left: 0;
  right: 0;
  top: 13px;
  height: 3px;
  background-color: #68b738;
  z-index: -1;
}
@media (min-width: 768px) {
