    padding: 0 37px;
    margin-right: -28px;
    margin-left: -36px;
  }
  .title--catalog {
    margin: 0;
  }
  .title::after {
    top: 22px;
    transform: translate(-4.5%);
  }
}
@media (min-width: 1300px) {
  .title {
    padding: 0 42px;
    margin-right: -44px;
    margin-left: -44px;
  }
  .title--catalog {
    padding: 0 37px;
