.main-nav__toggle {
  border: none;
  position: relative;
  background-color: transparent;
  display: block;
  margin-left: auto;
  margin-bottom: 4px;
  margin-right: 20px;
  width: 24px;
  height: 18px;
}
.main-nav__toggle--closed {
  background-image: url("../img/icon-mobile-menu.svg");
}
.main-nav__toggle--closed:active {
  opacity: 0.3;
}
.main-nav__toggle--opened {
  background-image: none;
  margin-top: 12px;
}
.main-nav__toggle--opened::before,
.main-nav__toggle--opened::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #111111;
  box-shadow: none;
}
.main-nav__toggle--opened::before:active::before,
.main-nav__toggle--opened::after:active::before,
.main-nav__toggle--opened::before:active::after,
.main-nav__toggle--opened::after:active::after {
  background-color: rgba(17, 17, 17, 0.3);
}
.main-nav__toggle--opened::before {
  transform: rotate(45deg);
}
.main-nav__toggle--opened::after {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .main-nav__toggle {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
